import React, { Component } from 'react';
import Page1 from './Page1';
import Page2 from './Page2';
import Page4 from './Page4New';
import { bindActionCreators } from 'redux';
import { getDictionary } from '../actions';
import { connect } from 'react-redux';
class Container extends Component {
  constructor(props) {
    super(props);
    this.state = { active: 1, profile: '' };
    this.setActive = this.setActive.bind(this);
  }
  componentDidMount() {
    this.props.getDictionary();
  }
  setActive(val) {
    this.setState({ active: val });
  }
  setProfile(name) {
    this.setState({ profile: name });
  }
  render() {
    const { active, profile } = this.state;
    return (
      <div className="App">
        {active === 1 && <Page1 nextPage={() => this.setActive(2)} />}
        {active === 2 && <Page2 nextPage={() => this.setActive(3)} profile={profile} />}
        {active === 3 && (
          <Page4 nextPage={() => this.setActive(2)} profile={profile} goBack={() => this.setActive(1)} />
        )}
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => {
  return bindActionCreators({ getDictionary }, dispatch);
};

export default connect(
  null,
  mapDispatchToProps
)(Container);



