import { DICTIONARY_POPULATE, ADD_TO_DICTIONARY } from '../constants';

const init_state = { dictionary: [] };

export default function dictionary(state = init_state, action) {
  switch (action.type) {
    case DICTIONARY_POPULATE: {
      return { ...state, dictionary: action.payload };
    }
    case ADD_TO_DICTIONARY: {
      return { ...state, dictionary: [...state.dictionary,action.payload] };
    }
    default:
      return state;
  }
}



