import React, { Component } from 'react';

export default class LineLoader extends Component {
  render() {
    const { percentage, statusText, marquee } = this.props;

    return (
      <div>
        <div className="line-loader-main">
          <div className="status-text-container">
            <div className="status-text">
              <h5 style={{ textAlign: 'left' }}>{statusText}</h5>
            </div>
            <div className="percent-value">
              {!marquee && <h5 style={{ textAlign: 'right' }}>{percentage}%</h5>}
            </div>
          </div>
          <div className="loader-container">
            {marquee && <div className="loader-line" />}
            {marquee===false && (
                <div
                  className="slider"
                  style={{
                    width: `${percentage}%`
                  }}
                />
              )}
          </div>
        </div>
      </div>
    );
  }
}



