import React, { Component } from 'react';
import './app1.scss';
import Container from './pages/Container';
import { Provider } from 'react-redux';
import store from './store'

class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <Container/>
      </Provider>
    );
  }
}

export default App;



