import React, { Component } from 'react';
import { isMobile } from 'react-device-detect';
class Page1 extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(val) {
    if (val == process.env.REACT_APP_PASSWORD) {
      this.props.nextPage();
    }
  }

  render() {
    return isMobile ? (
      <div className="page1">
        <div className="header-container" />
        <div className="page1-content">
          <div className="page1-form-content">
            <div className="page1-form-empty">
              Welcome to <br />
              OCR interface <br />
              By Bottle. <br />
              <br />
              Version 1.0 <br />
            </div>
            <div className="page1-content-text" />
            <div className="page1-input-field">
              <input
                type="number"
                placeholder="Enter Access Code"
                className="page1-text-input"
                onChange={e => this.handleChange(e.target.value)}
              />
            </div>
            <div className="page1-form-empty" />
          </div>
        </div>
      </div>
    ) : (
      <div className="page1-desktop">
        <div className="page1-desktop-content-container">
          <div className="page1-desktop-content">
          <div className="text-content">
            Welcome to <br />
            OCR interface <br />
            By Bottle. <br />
            <br />
            Version Control 1.0 <br />
            </div>
          </div>
          <div className="page1-input-field">
            <input
              type="number"
              placeholder="Enter Access Code"
              className="page1-text-input"
              onChange={e => this.handleChange(e.target.value)}
            />
          </div>
        </div>
      </div>
    );
  }
}
export default Page1;



