import React, { Component } from 'react';
import * as _ from 'lodash';
import './style.scss';
import SpanField from './SpanField';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { addToDictionary } from '../../actions';
class Editor extends Component {
  constructor(props) {
    super(props);
    this.state = { compArrayProps: [], text: '', tags: [] };
    this.setCorrection = this.setCorrection.bind(this);
    this.updateText = this.updateText.bind(this);
    this.addToDict = this.addToDict.bind(this);
  }

  setCorrection(key, value) {
    let newarr = this.state.compArrayProps;
    newarr[key].word = value;
    this.setState({ compArrayProps: newarr });
    let text = '';
    for (let i = 0; i < newarr.length; i++) {
      text = text + newarr[i].word;
    }
    this.props.changeText(text);
  }

  updateText() {
    const { compArrayProps } = this.state;
    let text = '';
    for (let i = 0; i < compArrayProps.length; i++) {
      text = text + compArrayProps[i].word;
    }
    this.props.changeText(text);
    this.props.retranslate(text);
  }

  componentDidMount() {
    this.mainHandler(this.props);
  }

  addToDict(word) {
    this.props.addToDictionary(word);
  }

  componentWillReceiveProps(newProps) {
    if (newProps.dictionary.length !== this.props.dictionary.length) {
      this.mainHandler(newProps);
    }
  }

  mainHandler(props) {
    console.log('mainhandler called',props);
    const { tags, text,dictionary } = props;
    const dataa = _.orderBy(tags, ['end_pos'], ['asc']);
    let str = text;
    const arr = [];
    arr.push({ word: str.substring(0, dataa[0].start_pos) });
    for (let i = 0; i < dataa.length; i++) {
      const wordvalue = str.substring(dataa[i].start_pos, dataa[i].end_pos + 1);
      if (_.indexOf(dictionary, wordvalue)!==-1) {
        arr.push({ word: wordvalue });
      } else {
        arr.push({
          word: wordvalue,
          hint: dataa[i].hint,
          suggestions: dataa[i].suggestions
        });
      }
      if (dataa[i + 1]) {
        arr.push({
          word: str.substring(dataa[i].end_pos + 1, dataa[i + 1].start_pos)
        });
      } else {
        arr.push({ word: str.substring(dataa[i].end_pos + 1, str.length) });
      }
    }
    console.log('proparr',arr);
    this.setState({ compArrayProps: arr });
  }

  render() {
    const { compArrayProps } = this.state;
    return (
      <div className="custom-textarea">
        <div className="ct-text-container">
          {compArrayProps.map((prop, idx) => (
            <Selector {...prop} keyid={idx} setCorrection={this.setCorrection} key={idx} addToDict={this.addToDict} />
          ))}
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => {
  return bindActionCreators({ addToDictionary }, dispatch);
};

const mapStateToProps = state => ({
  dictionary: state.dictionary.dictionary
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Editor);

const Selector = props => {
  return props.suggestions ? <SpanField {...props} /> : <span>{props.word}</span>;
};



