import {
  POPULATE_DATA,
  SET_CURRENT,
  REFRESH,
  BACK_TRANSLATE_START,
  BACK_TRANSLATE_SUCCESS,
  BACK_TRANSLATE_FAILURE,
  GET_GRAMMAR_START,
  GET_GRAMMAR_SUCCESS,
  GET_GRAMMAR_FAILURE,
  GET_EMOTION_START,
  GET_EMOTION_SUCCESS,
  GET_EMOTION_FAILURE,
  GET_WORDCOUNT_START,
  GET_WORDCOUNT_SUCCESS,
  GET_WORDCOUNT_FAILURE,
  DICTIONARY_POPULATE,
  ADD_TO_DICTIONARY
} from './../constants';

import { backTranslate, getEmotion, getGrammar, getWordCount, getDictionaryArray,addToDict } from '../api/api';

export function populateData(data) {
  return dispatch => {
    console.log(data);
    if (data.translations) {
      dispatch({ type: POPULATE_DATA, payload: data.translations });
    } else {
      const ar = [];
      ar.push(data);
      dispatch({ type: POPULATE_DATA, payload: ar });
    }
    dispatch({ type: REFRESH });
  };
}

export function changeTranslation(index, value) {
  return dispatch => {
    dispatch({ type: BACK_TRANSLATE_START, payload: index, english: value });
    backTranslate(value)
      .then(res => {
        const data = res.data;
        const payload = { index: '', data: { english: '', nepali: '' } };
        payload.index = index;
        payload.data.english = data.english;
        payload.data.nepali = data.nepali;
        dispatch({ type: BACK_TRANSLATE_SUCCESS, payload: payload });
        dispatch(populateEmotion(index, data.english));
        dispatch(populateGrammar(index, data.english));
        dispatch(populateWordCount(index, data.english));

      })
      .catch(err => {
        dispatch({ type: BACK_TRANSLATE_FAILURE, payload: index });
        dispatch(populateEmotion(index));
        dispatch(populateWordCount(index));
        dispatch(populateGrammar(index));
      });
  };
}
export function setCurrent(val) {
  return dispatch => {
    dispatch({ type: SET_CURRENT, payload: val });
    dispatch(checkAvailability(val - 1));
  };
}

export function checkAvailability(index) {
  return (dispatch, getState) => {
    const grammar = getState().grammar.data;
    const emotion = getState().emotion.data;
    const wordcount = getState().wordcount.data;
    if (!grammar[index]) {
      dispatch(populateGrammar(index));
    }
    if (!emotion[index]) {
      dispatch(populateEmotion(index));
    }
    if (!wordcount[index]) {
      dispatch(populateWordCount(index));
    }
  };
}

export function populateGrammar(index, val) {
  return (dispatch, getState) => {
    console.log(index,val, 'populating grammar nowww');
    const data = getState().output.data;
    dispatch({ type: GET_GRAMMAR_START, payload: index });
    const value = val ? val : data[index].english;
    getGrammar(value)
      .then(response => {
        const res = response.data;

        const payload = { gIndex: '', index: '',wordcount:'',issues:'',tags:[] };
        payload.index = index;
        console.log(res, 'grammar wala');
        if (res.result) {
          payload.gIndex = res.result.percentage;
          payload.wordcount = res.result.word_count;
          payload.issues = res.result.summaries[0].number_of_issues;
          payload.tags = res.result.tags;
        }
        console.log(payload);
        dispatch({ type: GET_GRAMMAR_SUCCESS, payload: payload });
      })
      .catch(err => dispatch({ type: GET_GRAMMAR_FAILURE, payload: index }));
  };
}

export function populateEmotion(index, val) {
  return (dispatch, getState) => {
    const data = getState().output.data;
    dispatch({ type: GET_EMOTION_START, payload: index });
    const value = val ? val : data[index].english;
    getEmotion(value)
      .then(response => {
        const res = response.data;
        console.log(res, 'emotion wala');

        const payload = { Happy: 0, Sad: 0, Excited: 0, Sarcasm: 0, Fear: 0, Bored: 0, index: '' };
        payload.index = index;
        if (res.code === 200 && res.emotion && res.emotion.probabilities) {
          payload.Happy = res.emotion.probabilities.Happy;
          payload.Sad = res.emotion.probabilities.Sad;
          payload.Excited = res.emotion.probabilities.Excited;
          payload.Sarcasm = res.emotion.probabilities.Sarcasm;
          payload.Fear = res.emotion.probabilities.Fear;
          payload.Bored = res.emotion.probabilities.Bored;
        }
        dispatch({ type: GET_EMOTION_SUCCESS, payload: payload });
      })
      .catch(err => dispatch({ type: GET_EMOTION_FAILURE, payload: index }));
  };
}

export function populateWordCount(index, val) {
  return (dispatch, getState) => {
    const data = getState().output.data;
    dispatch({ type: GET_WORDCOUNT_START, payload: index });
    const value = val ? val : data[index].english;
    getWordCount(value)
      .then(response => {
        const payload = { count: '', index: '', occurence: [] };
        payload.index = index;
        console.log(response, 'wordcount wala');
        if (response.data) {
          const res = response.data;

          payload.occurence = res.occurence;
          payload.count = res.wordcount;
        }
        dispatch({ type: GET_WORDCOUNT_SUCCESS, payload: payload });
      })
      .catch(err => dispatch({ type: GET_WORDCOUNT_FAILURE, payload: index }));
  };
}


export function getDictionary() {
  return dispatch => {
    getDictionaryArray().then((res)=>dispatch({type: DICTIONARY_POPULATE,payload:res.data}))
  };
}
export function addToDictionary(word) {
  return dispatch => {
    addToDict(word).then((res)=>dispatch({type: ADD_TO_DICTIONARY,payload:word}))
  };
}


