import { REFRESH, GET_EMOTION_START, GET_EMOTION_SUCCESS, GET_EMOTION_FAILURE } from './../constants'; // for importing constants

const initial_state = { data: {} };

export default function emotion(state = initial_state, action) {
  let newState = JSON.parse(JSON.stringify(state)); //change newstate values and send it as normal,prevents mutation of state ;)

  switch (action.type) {
    case REFRESH:
      newState.data = {};
      return newState;
    case GET_EMOTION_START:
      newState.data[action.payload] = { loading: true, happy: 0, sad: 0, excited: 0, sarcasm: 0, fear: 0, bored: 0 };
      return newState;
    case GET_EMOTION_SUCCESS:
      newState.data[action.payload.index] = {
        loading: false,
        happy: action.payload.Happy,
        sad: action.payload.Sad,
        excited: action.payload.Excited,
        sarcasm: action.payload.Sarcasm,
        fear: action.payload.Fear,
        bored: action.payload.Bored
      };
      return newState;
      case GET_EMOTION_FAILURE:
      newState.data[action.payload] = { loading: false, happy: 0, sad: 0, excited: 0, sarcasm: 0, fear: 0, bored: 0 };
      return newState;
    default:
      return state;
  }
}



