import { REFRESH, GET_WORDCOUNT_START, GET_WORDCOUNT_SUCCESS, GET_WORDCOUNT_FAILURE } from './../constants'; // for importing constants

const initial_state = { data: {} };

export default function wordcount(state = initial_state, action) {
  let newState = JSON.parse(JSON.stringify(state)); //change newstate values and send it as normal,prevents mutation of state ;)

  switch (action.type) {
    case REFRESH:
      newState.data = {};
      return newState;
    case GET_WORDCOUNT_START:
      newState.data[action.payload] = { loading: true, count: '', occurence: [] };
      return newState;
    case GET_WORDCOUNT_SUCCESS:
      newState.data[action.payload.index] = {
        loading: false,
        count: action.payload.count,
        occurence: action.payload.occurence
      };
      return newState;
    case GET_WORDCOUNT_FAILURE:
      newState.data[action.payload] = { loading: false, count: '', occurence: [] };
      return newState;
    default:
      return state;
  }
}



