import React, { Component } from 'react';
import Slider from '../../components/Slider';
import { ic_chevron_left } from 'react-icons-kit/md/ic_chevron_left';
import { Icon } from 'react-icons-kit';
import Spinner from '../../components/spinner';

class Mobile extends Component {
  render() {
    const { emotion, current, wordcount,grammar } = this.props;
    return (
      <div className="page4-sentiment">
        <div className="header-container">
          <div className="icon" onClick={() => this.props.onClose()}>
            <Icon icon={ic_chevron_left} size={24} />
          </div>
        </div>
        <div className="overview-container">
          <h2>Overview</h2>
          <div className="overview-topics">
            <div className="overview-topic-left">
              <h4>Language:</h4>
              <h4>Number of Words:</h4>
              <h4>Most Used Words:</h4>
            </div>
            <div className="overview-topic-right">
              {/* language*/}
              <h4>British English</h4>
              {/* no of words */}
              <h4>{grammar[current-1].wordcount}</h4>
            </div>
          </div>
          <div className="most-used-words">
           
            {wordcount[current - 1].occurence.map((item, id) => <h4 key={id}>{item['0']}</h4>)}
            {wordcount[current - 1].loading && <Spinner />}
          </div>
        </div>
        <div className="grey-bar" />
        <div className="context-container">
          <h2 className="header">Context Analysis</h2>
          {emotion[current - 1] && !emotion[current - 1]['loading'] && (
            <div className="slider-components">
              <div className="slider-main-container">
                <Slider percentage={(emotion[current - 1]['happy'] * 100).toFixed(2)} name="Happy" />
              </div>
              <div className="slider-main-container">
                <Slider percentage={(emotion[current - 1]['sad'] * 100).toFixed(2)} name="Sad" />
              </div>
              <div className="slider-main-container">
                <Slider percentage={(emotion[current - 1]['excited'] * 100).toFixed(2)} name="Excited" />
              </div>
              <div className="slider-main-container">
                <Slider percentage={(emotion[current - 1]['sarcasm'] * 100).toFixed(2)} name="Sarcasm" />
              </div>
              <div className="slider-main-container">
                <Slider percentage={(emotion[current - 1]['fear'] * 100).toFixed(2)} name="Fear" />
              </div>
              <div className="slider-main-container">
                <Slider percentage={(emotion[current - 1]['bored'] * 100).toFixed(2)} name="Bored" />
              </div>
            </div>
          )}
          {emotion[current - 1] && emotion[current - 1]['loading'] && (
            <div className="slider-components">
              <Spinner />
            </div>
          )}
          {!emotion[current - 1] && <iv className="slider-components" />}
        </div>
      </div>
    );
  }
}

export default Mobile;



