import React, { Component } from 'react';
import Header from '../components/Header';
import Button from '../components/Button';
import LineLoader from '../components/LineLoader';
import { isMobile } from 'react-device-detect';
import { Icon } from 'react-icons-kit';
import { ic_attach_file } from 'react-icons-kit/md/ic_attach_file';
import Dropzone from 'react-dropzone';
import { translate } from '../api/api';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as allActions from './../actions'
class Page2 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      photo: '',
      file: '',
      loading: false,
      spinner: false,
      percentage: 0,
      statustext: 'Recognising',
      styleval: ''
    };
    this.handleChange = this.handleChange.bind(this);
    this.timer = this.timer.bind(this);
    this.startIncrease = this.startIncrease.bind(this);
    this.setOutputData = this.setOutputData.bind(this);
  }

  handleChange(event) {
    const that = this;
    this.setState({ spinner: true, loading: true });
    const file = event.target.files[0];
    const formData = new FormData();
    formData.append('photo', file, file.name);
    translate(formData)
      .then(res => {
        if (res.data) {
          that.props.action.populateData(res.data);
          that.startIncrease();
        }
      })
      .catch(err => alert(err));
  }

  setOutputData(data) {
    this.props.setOutput(data);
  }

  onDrop(files) {
    const that = this;
    this.setState({ spinner: true, loading: true });
    const file = files[0];
    const formData = new FormData();
    formData.append('photo', file, file.name);
    translate(formData)
      .then(res => {
        if (res.data) {
          that.props.action.populateData(res.data);
          that.startIncrease();
        }
      })
      .catch(err => alert(err));
  }

  startIncrease() {
    this.interval = setInterval(this.timer, 50);
    this.setState({ spinner: false });
  }
  timer() {
    if (this.state.percentage >= 100) {
      clearInterval(this.interval);
      this.props.nextPage();
    }
    this.setState({ percentage: this.state.percentage + 1 });

    if (this.state.percentage > 15 && this.state.percentage < 30) {
      this.setState({ statustext: 'Kernelling' });
    } else if (this.state.percentage > 30 && this.state.percentage < 70) {
      this.setState({ statustext: 'Translating' });
    } else if (this.state.percentage > 70 && this.state.percentage < 99) {
      this.setState({ statustext: 'Contextualising' });
    } else {
    }
  }

  render() {
    const { profile } = this.props;
    return (
      <div>
        {isMobile && (
          <div className="App">
            {this.state.loading === false ? (
              <div className="page2">
                <div className="header-container">
                  <Header name={profile || 'Abi'} />
                </div>
                <div className="page2-camera">
                  <div className="image-upload">
                    <span>
                      <label htmlFor="file-input" className="icon-color">
                        <h2 style={{ textAlign: 'center' }}>Scan</h2>
                      </label>
                      <p
                        style={{
                          textAlign: 'center',
                          fontSize: '1em',
                          fontWeight: 'bold',
                          opacity: '0.7',
                          width: '100%',
                          color: 'grey'
                        }}
                      >
                        Or
                      </p>
                      <label htmlFor="file-input2" className="icon-color">
                        <Button text="Upload" onClick={() => console.log('clicked')} />
                      </label>
                    </span>
                    <input
                      id="file-input"
                      type="file"
                      accept="image/*"
                      capture
                      onChange={this.handleChange}
                      className=""
                    />
                    <input
                      id="file-input2"
                      type="file"
                      accept="image/jpeg,image/gif,image/png,application/pdf,image/x-eps"
                      onChange={this.handleChange}
                    />
                  </div>
                </div>
                <div className="page2-camera-clicker-container" />
              </div>
            ) : (
              <Page2Grad
                styleval={this.state.styleval}
                percentage={this.state.percentage}
                spinner={this.state.spinner}
                statusText={this.state.statustext}
              />
            )}
          </div>
        )}
        {!isMobile && (
          <div className="page2-desktop">
            <div className="header-container">
              <Header name={profile || 'Abi'} />
            </div>

            <div className="page2-desktop-content-container">
              <div className="page2-desktop-content">
                {this.state.loading ? (
                  <Page2Grad
                    styleval={this.state.styleval}
                    percentage={this.state.percentage}
                    spinner={this.state.spinner}
                    statusText={this.state.statustext}
                  />
                ) : (
                  <Dropzone onDrop={this.onDrop.bind(this)} className="file-input-container">
                    <div className="icon-container">
                      <label className="icon-color">
                        <Icon icon={ic_attach_file} size={128} />
                      </label>
                    </div>
                    <br />
                    <div className="button-container">
                      <label className="icon-color">
                        <Button text="Upload or Drag the File" onClick={() => console.log('clicked')} />
                      </label>
                    </div>
                  </Dropzone>
                )}
              </div>
            </div>
          </div>
        )}
      </div>
    );
  }
}
const mapDispatchToProps = dispatch => {
  return {
  action: bindActionCreators(allActions, dispatch),
  };
  };
  
  const mapStateToProps = state => ({
  
  });
  
  export default connect(
  mapStateToProps,
  mapDispatchToProps
  )(Page2);
  
class Page2Grad extends Component {
  render() {
    const { styleval, percentage, statusText, spinner } = this.props;
    return (
      <div className={`page2-grad-container ${styleval}`}>
        {spinner && <LineLoader marquee percentage={0} statusText="Recognising" />}
        {!spinner && <LineLoader percentage={percentage} marquee={false} statusText={statusText} />}
      </div>
    );
  }
}



