import React, { Component } from "react";
import onClickOutside from "react-onclickoutside";

class SpanField extends Component {
  constructor(props) {
    super(props);
    this.state = {
      suggestionOpen: false,
      edited: false,
      value: "",
      position: { top: "", left: "" }
    };
    this.openSugg = this.openSugg.bind(this);
    this.onSuggestionClick= this.onSuggestionClick.bind(this);
  }
  onSuggestionClick(key, value) {
    this.setState({ value: value, suggestionOpen: false, edited: true });
    this.props.setCorrection(key,value);
  };
  handleClickOutside = evt => {
    this.setState({ suggestionOpen: false });
  };
  openSugg(event) {
    event.preventDefault();
    let x = event.clientX || (event.touches && event.touches[0].pageX);
    let y = event.clientY || (event.touches && event.touches[0].pageY);
    this.setState({ position: { top: y, left: x }, suggestionOpen: true });
    console.log(event);
  }
  render() {
    const { keyid, word, suggestions, hint } = this.props;
    const { suggestionOpen, edited, value } = this.state;
    return (
      <span>
        <span
          className={edited ? "normal-text" : "highlighted-text"}
          onClick={this.openSugg}
        >
          {edited ? value : word}
        </span>

        {suggestionOpen && (
          <span className="sugg-box" style={this.state.position}>
            <p className="hint">{hint}</p>
            <ul className="sugg-list">
            <li onClick={()=>{this.setState({suggestionOpen:false});this.props.addToDict(word)}} className="add-to-dict">Add to Dictionary</li>
              {suggestions.map((sug, idx) => (
                <li
                  key={idx}
                  className="sugg"
                  onClick={() => {this.onSuggestionClick(keyid, sug)}}
                >
                  {sug}
                </li>
              ))}
            </ul>
          </span>
        )}
      </span>
    );
  }
}

export default onClickOutside(SpanField);



