import React, { Component } from 'react';
import MediaQuery from 'react-responsive';
import Desktop from './Desktop';
import Mobile from './Mobile';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import * as actionCreator from './../../actions';

class Page extends Component {
  constructor(props) {
    super(props);
    this.state = { outputPage: true, editing: false, newval: '' };
    this.previous = this.previous.bind(this);
    this.next = this.next.bind(this);
    this.set = this.set.bind(this);
    this.retranslate = this.retranslate.bind(this);
    this.handleEdit = this.handleEdit.bind(this);
  }

  componentDidMount() {
    this.props.action.checkAvailability(this.props.current - 1);
  }
  previous(current) {
    if (current > 1) {
      this.props.action.setCurrent(current - 1);
    }
    console.log('previous', current);
  }

  next(current) {
    if (current < this.props.output.length) {
      this.props.action.setCurrent(current + 1);
    }
    console.log('next', current);
  }

  set(key, value) {
    this.setState({ [key]: value });
  }

  retranslate(val) {
    if (this.state.newval) {
      this.props.action.changeTranslation(this.props.current - 1, this.state.newval);
    }
  }
  handleEdit(val) {
    this.setState({ newval: val });
  }
  render() {
    const { outputPage, editing, newval } = this.state;
    return (
      <div style={{ display: 'flex', height: '100vh', width: '100vw' }}>
        <MediaQuery query="(max-width: 768px)">
          <Mobile
            {...this.props}
            set={this.set}
            previous={this.previous}
            next={this.next}
            retranslate={this.retranslate}
            outputPage={outputPage}
            editing={editing}
            newval={newval}
            handleEdit={this.handleEdit}
          />
        </MediaQuery>
        <MediaQuery query="(min-width: 769px)">
          <Desktop
            {...this.props}
            set={this.set}
            previous={this.previous}
            next={this.next}
            retranslate={this.retranslate}
            outputPage={outputPage}
            editing={editing}
            newval={newval}
            handleEdit={this.handleEdit}
          />
        </MediaQuery>
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => {
  return {
    action: bindActionCreators(actionCreator, dispatch)
  };
};

const mapStateToProps = state => ({
  current: state.output.current,
  output: state.output.data,
  grammar: state.grammar.data,
  emotion: state.emotion.data,
  wordcount: state.wordcount.data
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Page);



