import React, { Component } from "react";
import './slider.scss'
class Slider extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    const { percentage, name } = this.props;
    return (
      <div className="slider-main">
        <div className="slider-name">{name}:</div>
        <div className="slider-container">
          <div className="slider-ball" style={{ left: `${percentage}%` }} />
          <div className="slider-line" />
        </div>
        <div className="slider-percentage">{percentage}%</div>
      </div>
    );
  }
}
export default Slider;


