import {
  BACK_TRANSLATE_START,
  BACK_TRANSLATE_SUCCESS,
  BACK_TRANSLATE_FAILURE,
  POPULATE_DATA,
  SET_CURRENT
} from '../constants';

const initial_state = {
  data: [],
  current: 1
};

export default function output(state = initial_state, action) {
  let newState = JSON.parse(JSON.stringify(state));

  switch (action.type) {
    case POPULATE_DATA:
      newState.data = action.payload;
      return newState;
    case SET_CURRENT:
      newState.current = action.payload;
      return newState;
    case BACK_TRANSLATE_START:
      newState.data[action.payload] = { loading: true, english: '', nepali: '' };
      newState.data[action.payload].english = action.english;
      return newState;
    case BACK_TRANSLATE_SUCCESS:
      newState.data[action.payload.index] = { loading: false, english: '', nepali: '' };

      newState.data[action.payload.index].english = action.payload.data.english;
      newState.data[action.payload.index].nepali = action.payload.data.nepali;
      return newState;
    case BACK_TRANSLATE_FAILURE:
      newState.data[action.payload] = { loading: false, english: '', nepali: '' };
      newState.data[action.payload].english = '';
      newState.data[action.payload].nepali = '';
      return newState;
    default:
      return state;
  }
}



